"use client";

import Image from "next/image";
import { useMemo, useState } from "react";

type LogItem = {
  id: string;
  name: string;
  policeStation: string;
  matches: number;
  confidence: number; // 0..1
  images: string[];
};

const MOCK_LOGS: LogItem[] = [
  {
    id: "1",
    name: "John Carter",
    policeStation: "PS-12 Alpha",
    matches: 3,
    confidence: 0.92,
    images: ["/next.svg", "/vercel.svg", "/globe.svg"],
  },
  {
    id: "2",
    name: "Sarah Connor",
    policeStation: "PS-7 Beta",
    matches: 1,
    confidence: 0.45,
    images: ["/window.svg", "/file.svg"],
  },
  {
    id: "3",
    name: "Neo Anderson",
    policeStation: "PS-3 Zion",
    matches: 2,
    confidence: 0.78,
    images: ["/vercel.svg", "/next.svg"],
  },
];

export default function Home() {
  const [threshold, setThreshold] = useState(0.6);
  const [expandedId, setExpandedId] = useState<string | null>(null);
  const [slideIndex, setSlideIndex] = useState<Record<string, number>>({});

  const filteredLogs = useMemo(
    () => MOCK_LOGS.filter((l) => l.confidence >= threshold),
    [threshold]
  );

  const onNext = (id: string, total: number) => {
    setSlideIndex((prev) => {
      const current = prev[id] ?? 0;
      return { ...prev, [id]: (current + 1) % total };
    });
  };

  const onPrev = (id: string, total: number) => {
    setSlideIndex((prev) => {
      const current = prev[id] ?? 0;
      return { ...prev, [id]: (current - 1 + total) % total };
    });
  };

  return (
    <div className="min-h-screen w-full px-4 sm:px-8 py-8">
      <div className="mx-auto max-w-5xl">
        <h1 className="text-3xl sm:text-4xl font-bold tracking-widest text-center text-[#9ad7ff] drop-shadow-[0_0_10px_rgba(0,209,255,0.25)]">
          FRIGATE FACE LOGS
        </h1>

        <div className="mt-8 glass neon-border rounded-xl p-5">
          <div className="flex items-center gap-4">
            <span className="text-sm text-[#9ad7ff]/80">Threshold</span>
            <input
              type="range"
              min={0.2}
              max={1}
              step={0.01}
              value={threshold}
              onChange={(e) => setThreshold(parseFloat(e.target.value))}
              className="w-full accent-[#00d1ff]"
            />
            <span className="text-sm tabular-nums text-[#00d1ff]">
              {threshold.toFixed(2)}
            </span>
          </div>
        </div>

        <div className="mt-6 space-y-4">
          {filteredLogs.map((log) => {
            const isExpanded = expandedId === log.id;
            const index = slideIndex[log.id] ?? 0;
            const total = log.images.length;
            return (
              <div
                key={log.id}
                className="glass neon-border rounded-xl p-5 transition-shadow hover:shadow-[0_0_20px_rgba(0,209,255,0.2)]"
              >
                <button
                  onClick={() =>
                    setExpandedId((prev) => (prev === log.id ? null : log.id))
                  }
                  className="w-full text-left"
                >
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-4">
                      <div className="h-10 w-10 rounded-full bg-gradient-to-br from-[#0ea5e9] to-[#00d1ff] flex items-center justify-center text-black font-bold">
                        {log.name.charAt(0)}
                      </div>
                      <div>
                        <div className="font-semibold text-[#e6f3ff]">
                          {log.name}
                        </div>
                        <div className="text-xs text-[#9ad7ff]/70">
                          Station: {log.policeStation}
                        </div>
                      </div>
                    </div>
                    <div className="text-right">
                      <div className="text-sm text-[#9ad7ff]">
                        Matches: {log.matches}
                      </div>
                      <div className="text-xs text-[#00d1ff]">
                        Confidence: {(log.confidence * 100).toFixed(0)}%
                      </div>
                    </div>
                  </div>
                </button>

                {isExpanded && (
                  <div className="mt-4 border-t border-[#0b2d4a] pt-4">
                    <div className="relative overflow-hidden rounded-lg bg-black/40">
                      <div className="flex items-center justify-center h-56">
                        <Image
                          src={log.images[index]}
                          alt={`${log.name} ${index + 1}`}
                          width={200}
                          height={200}
                          className="object-contain"
                        />
                      </div>
                      <div className="absolute inset-0 flex items-center justify-between px-2">
                        <button
                          onClick={() => onPrev(log.id, total)}
                          className="h-10 w-10 rounded-full bg-[#0b132b]/70 hover:bg-[#0b132b]/90 text-[#9ad7ff] grid place-items-center"
                          aria-label="Previous image"
                        >
                          ‹
                        </button>
                        <button
                          onClick={() => onNext(log.id, total)}
                          className="h-10 w-10 rounded-full bg-[#0b132b]/70 hover:bg-[#0b132b]/90 text-[#9ad7ff] grid place-items-center"
                          aria-label="Next image"
                        >
                          ›
                        </button>
                      </div>
                      <div className="absolute bottom-2 left-1/2 -translate-x-1/2 flex gap-2">
                        {log.images.map((_, i) => (
                          <span
                            key={i}
                            className={`h-1.5 w-6 rounded-full ${
                              i === index ? "bg-[#00d1ff]" : "bg-[#16324a]"
                            }`}
                          />
                        ))}
                      </div>
                    </div>

                    <div className="mt-4 flex justify-end">
                      <button
                        onClick={() => setExpandedId(null)}
                        className="px-4 py-2 rounded-lg bg-gradient-to-r from-[#0ea5e9] to-[#00d1ff] text-black font-semibold tracking-wide hover:opacity-90"
                      >
                        Collapse
                      </button>
                    </div>
                  </div>
                )}
              </div>
            );
          })}

          {filteredLogs.length === 0 && (
            <div className="glass neon-border rounded-xl p-6 text-center text-[#9ad7ff]/80">
              No logs at this threshold.
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
